<template lang="html">
  <div>
    <h1>Recents</h1>
    <md-list class="custom-list md-triple-line" v-for="item in data.api.data.inactive">
      <md-list-item>
        <md-avatar>
          <img src="./../assets/red.png">
        </md-avatar>

        <div class="md-list-text-container">
          <span># {{item.count}}</span>
          <h3>InActive Point :</h3>
        <div v-for="location in item.locations">
            <b id="location">{{location.location}} {{locat(location.location)}}</b>
        </div>
        </div>
        <md-divider class="md-inset"></md-divider>
      </md-list-item>
    </md-list>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      uri: 'https://api-iot.herokuapp.com/api/',
      inActive: ''
    }
  },
  mounted () {
    // this.$http.get(this.uri + 'inactive').then((res) => {
    //   console.log(res.data)
    //   this.inActive = res.data
    // })
  },
  computed: {
    ...mapState([
      'data'
    ])
  },
  methods: {
    locat: function (num) {
      if (num === '1') {
        return 'หน้าห้องพวงครวม 2'
      } else if (num === '2') {
        return 'ห้องนำ้หน้ามินิมาร์ท'
      } else if (num === '3') {
        return 'ห้องนำ้สำนักคอม'
      } else if (num === '4') {
        return 'หน้าห้องพยาบาล'
      } else if (num === '5') {
        return 'หน้าห้องนำ้ลิฟหลัง'
      } else if (num === '6') {
        return 'ลานกลางฝั่งสำนักงานคณะบดี'
      }
    }
  }
}
</script>

<style lang="css">

</style>



